import cl from '../QRCodeSettings.module.scss'

const InputRadio = ({ label, name, id, value, defaultChecked = false, onChange }) => {
  return (
    <div>
      <input
        type='radio'
        name={ name }
        id={ id }
        value={ value }
        defaultChecked={ defaultChecked }
        onChange={ onChange }
      />
      <label htmlFor={ id }>{ label }</label>
    </div>
  )
}

export default InputRadio