import cl from '../QRCodeSettings.module.scss'

const InputNumber = ({ label, id, option = undefined, options, setOptions, minMax, isGradient = false, defaultValue }) => {

  const onChangeHandler = (e) => {
    if (isGradient) {

      let gradient = Object.assign({}, options.gradient)
      gradient.rotation = (Math.PI * +e.target.value) / 180

      setOptions({
        ...options,
        gradient: {
          ...gradient,
        }
      })
    } else {
      setOptions({
        ...options,
        [option]: +e.target.value,
      })

    }
  }

  return (
    <div className={ cl.input__wrapper }>
      <label htmlFor={ id }>
        { label }
      </label>
      <input
        type='number'
        min={ minMax[0] }
        max={ minMax[1] }
        id={ id }
        className={ `${cl.settings__input} ${cl.input_number}` }
        defaultValue={ defaultValue }
        onChange={ e => onChangeHandler(e) }
      />
    </div>
  )
}

export default InputNumber