// import React, { useEffect, useState } from 'react'
import cl from './QRCodeSettings.module.scss'
import InputNumber from './InputNumber/InputNumber'
// import QRCodeStyling from 'qr-code-styling'
import InputColor from './InputColor/InputColor'
import InputList from './InputList/InputList'
import InputGradient from './InputGradient/InputGradient'
import { useState } from 'react'
import SectionColorType from './SectionColorType/SectionColorType'

const QRCodeSettings = ({ generalOptions, setGeneralOptions, dotsOptions, setDotsOptions, backgroundOptions, setBackgroundOptions, qrCode }) => {

  return (
    <div className={ cl.settings }>
      <h3 className={ cl.settings__title }>Настройки</h3>

      <div className={ cl.settings__section }>
        <span className={ cl.section__title }>Общие</span>

        <div className={ cl.section__inputs }>

          <InputNumber
            label='Ширина'
            id='width'
            option='width'
            options={ generalOptions }
            setOptions={ setGeneralOptions }
            minMax={ [20, 2000] }
            defaultValue={ generalOptions.width }
          />

          <InputNumber
            label='Высота'
            id='height'
            option='height'
            options={ generalOptions }
            setOptions={ setGeneralOptions }
            minMax={ [20, 2000] }
            defaultValue={ generalOptions.height }
          />

          <InputNumber
            label='Отступ'
            id='margin'
            option='margin'
            options={ generalOptions }
            setOptions={ setGeneralOptions }
            minMax={ [0, 200] }
            defaultValue={ generalOptions.margin }
          />

        </div>

      </div>

      <div className={ cl.settings__section }>
        <span className={ cl.section__title }>Точки</span>
        <div className={ cl.section__inputs }>

          <InputColor
            label='Цвет'
            id='color'
            defaultValue={ dotsOptions.color }
            options={ dotsOptions }
            setOptions={ setDotsOptions }
          />

          <InputList
            label='Стиль'
            id='dots-type'
            optionClass='dotsOptions'
            option='type'
            qrCode={ qrCode }
          />

        </div>

      </div>

      <div className={ cl.settings__section }>

        <span className={ cl.section__title }>Фон</span>

        <div className={ cl.section__inputs }>
          <SectionColorType
            options={ backgroundOptions }
            setOptions={ setBackgroundOptions }
            optionClass='backgroundOptions'
            qrCode={ qrCode }
          />
        </div>

      </div>

    </div>
  )
}

export default QRCodeSettings