import { useEffect, useRef, useState } from 'react'
import InputColor from '../InputColor/InputColor'
import InputGradient from '../InputGradient/InputGradient'
import InputRadio from '../InputRadio/InputRadio'
import cl from '../QRCodeSettings.module.scss'

const SectionColorType = ({ options, setOptions, optionClass, qrCode }) => {
  // console.log(options);
  // console.log(setOptions);
  const [colorType, setColorType] = useState('solid')

  const renderColorInputs = () => {
    switch (colorType) {
      case 'solid':
        return (
          <InputColor
            label='Цвет'
            id='color'
            defaultValue={ options.color }
            options={ options }
            setOptions={ setOptions }
            isGradient={ false }
          />
        )
        break
      case 'gradient':
        return (
          <InputGradient
            options={ options }
            setOptions={ setOptions }
            optionClass={ optionClass }
            qrCode={ qrCode }
          />
        )
        break
    }
  }

  const changeColorType = e => {
    switch (e.target.value) {
      case 'solid':
        setOptions({color: '#ffffff',})
        setColorType('solid')
        break
      case 'gradient':
        setOptions({
          gradient: {
            type: 'linear',
            rotation: 90,
            colorStops: [{offset: 0, color: '#ced4da'}, {offset: 1, color: '#f8f9fa'}],
          },
        })
        setColorType('gradient')
      break
    }
  }

  return (
    <div className={ cl.section__colorType }>
      <div className={ cl.colorType__inputs }>
        <InputRadio
          label='Сплошной'
          name={ optionClass }
          id='bgSolid'
          value='solid'
          defaultChecked={ true }
          onChange={ changeColorType }
        />

        <InputRadio
          label='Градиент'
          name={ optionClass }
          id='bgGradient'
          value='gradient'
          onChange={ changeColorType }
        />
      </div>

      { renderColorInputs() }

    </div>
  )
}

export default SectionColorType