import cl from '../QRCodeSettings.module.scss'

const InputColor = ({ label, id, defaultValue, options, setOptions, isGradient }) => {

  const onChangeHandler = (e) => {
    if (isGradient) {

      let colorStops = options.gradient.colorStops
      colorStops[isGradient[1]].color = e.target.value

      setOptions({
        ...options,
        gradient: {
          colorStops: [
            ...colorStops,
          ]
        }
      })

    } else {
      setOptions({ color: e.target.value })
    }
  }

  return (
    <div className={ cl.input__wrapper }>
      <label htmlFor={ id }>{ label }</label>
      <input
        type='color'
        id={ id }
        className={ `${cl.settings__input} ${cl.input_color}` }
        defaultValue={ defaultValue }
        onChange={ e => onChangeHandler(e) }
      />
    </div>
  )
}

export default InputColor