import InputColor from '../InputColor/InputColor';
import InputNumber from '../InputNumber/InputNumber';
import cl from '../QRCodeSettings.module.scss'

const InputGradient = ({ options, setOptions }) => {

  const onAngleChangeHandler = e => {

    let gradient = Object.assign({}, options.gradient)
    gradient.rotation = (Math.PI * +e.target.value) / 180

    setOptions({
      ...options,
      gradient: {
        ...gradient,
      }
    })
  }

  return (
    <div className={ cl.input__gradient }>

      <InputColor
        label='Цвет 1'
        id='bgGradColor1'
        defaultValue='#ced4da'
        options={ options }
        setOptions={ setOptions }
        isGradient={ [true, 0] }
      />

      <InputColor
        label='Цвет 2'
        id='bgGradColor2'
        defaultValue='#f8f9fa'
        options={ options }
        setOptions={ setOptions }
        isGradient={ [true, 1] }
      />

      <InputNumber
        label='Угол'
        id='bgGradRotation'
        options={ options }
        setOptions={ setOptions }
        minMax={ [0, 360] }
        isGradient={ true }
        defaultValue={ options.gradient.rotation }
      />

    </div>
  )
}

export default InputGradient